<template>
  <div class="scroll_body">
    <loading :active.sync="isLoading" :is-full-page="false"></loading>
    <button hidden id="showDeleteModalBtn" data-toggle="modal" data-target="#deleteModal"></button>
    <button hidden id="showShareModalBtn" data-toggle="modal" data-target="#shareModal"></button>
    <button hidden id="showFreezeModalBtn" data-toggle="modal" data-target="#freezeModal"></button>
    <DeletePostModal
      v-on:delete="delete_post"
      v-on:freeze="callDeleteAction();callFreezeAction()"
      v-on:edit="callDeleteAction(),callEditAction()"
    ></DeletePostModal>
    <SharePostModal
      v-if="posts.length"
      v-on:commit_share="share_post"
      :shareLink="posts[active_index].shareLink"
      :shareOn="posts[active_index].shareLinkStatus"
      @updateShareLinkStatus="updateShareLinkStatus"
    ></SharePostModal>
    <FreezePostModal v-on:freeze="freeze_post"></FreezePostModal>
    <NewPostEditor class="shadow" :user="user" @new_post_created="refreshPosts" :editMode="false"></NewPostEditor>
    <div class="my-3" v-for="(post, index) in posts" v-bind:key="index">
      <Post
        v-if="posts.length"
        :index="index"
        :post="post"
        v-on:delete_action="callDeleteAction"
        v-on:freeze_action="callFreezeAction"
        v-on:share_action="callShareAction"
        v-on:edit_action="callEditAction"
        v-on:post_updated="updatePost"
        :user="user"
      />
    </div>
  </div>
</template>

<script>
import NewPostEditor from "./new_post_components/NewPostEditor";
import Post from "./post_components/Post";
import postRepository from "../../repositories/postRepository";
import DeletePostModal from "./post_components/DeletePostModal";
import SharePostModal from "./post_components/SharePostModal";
import FreezePostModal from "./post_components/FreezePostModal";
import Loading from "vue-loading-overlay";

export default {
  props: { user: Object },
  data() {
    return {
      isLoading: false,
      active_index: 0,
      posts: []
    };
  },
  components: {
    NewPostEditor,
    Post,
    DeletePostModal,
    FreezePostModal,
    SharePostModal,
    Loading
  },
  async created() {
    try {
      this.isLoading = true;
      let result = await postRepository.getListPost(0);
      console.log(result);
      this.posts = result.posts;
    } catch (e) {
      console.log(e);
    } finally {
      this.isLoading = false;
    }
  },
  methods: {
    callDeleteAction(e) {
      //hàm chỉ có tác dụng gọi modal xác thực
      console.log(e);
      document.getElementById("showDeleteModalBtn").click();
      if (e != undefined && e.index != undefined) this.active_index = e.index;
    },
    callEditAction(e) {
      console.log(e);
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      console.log("edit in ", this.active_index);
      //hàm chỉ có tác dụng gọi modal xác thực
      // document.getElementById("showDeleteModalBtn").click();
      // this.active_index = e.index;
    },
    callShareAction(e) {
      console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showShareModalBtn").click();
    },
    callFreezeAction(e) {
      console.log(e);
      //hàm chỉ có tác dụng gọi modal xác thực
      if (e != undefined && e.index != undefined) this.active_index = e.index;
      document.getElementById("showFreezeModalBtn").click();
    },
    async edit_post(e) {},
    async share_post(e) {
      //đóng modal
      document.getElementById("showShareModalBtn").click();
      try {
        let listEmail = e.emails;
        console.log(listEmail);
        let listSuccessSent = await postRepository.share_post_to_email(
          this.posts[this.active_index]._id,
          listEmail
        );
        //TODO hiển thị danh sách email đã gửi thành công
      } catch (error) {
        //TODO hiển thị thông báo lỗi
      }
    },
    async updateShareLinkStatus(e) {
      try {
        this.isLoading = true;
        console.log("status", e.status);
        await postRepository.updatePostShareLinkStatus(
          this.posts[this.active_index]._id,
          e.status.checked
        );
        this.posts[this.active_index].shareLinkStatus = status;
      } catch (error) {
        console.log("lỗi bật/tắt link share", error);
      } finally {
        this.isLoading = false;
      }
    },
    async freeze_post() {
      //đóng modal
      document.getElementById("showFreezeModalBtn").click();
      console.log("triggle freeze");
      try {
        this.isLoading = true;
        await postRepository.freezePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
      } catch (error) {
        console.log("lỗi đóng băng kỉ niệm", error);
      } finally {
        this.isLoading = false;
      }
    },
    async delete_post() {
      //đóng modal
      document.getElementById("showDeleteModalBtn").click();
      console.log("triggle delete");
      try {
        this.isLoading = true;
        await postRepository.deletePost(this.posts[this.active_index]._id);
        this.posts.splice(this.active_index, 1);
      } catch (error) {
        console.log("lỗi xóa kỉ niệm", error);
      } finally {
        this.isLoading = false;
      }
    },
    async refreshPosts(filter = null) {
      try {
        this.isLoading = true;
        let result = await postRepository.getListPost(0, filter);
        this.posts = result.posts;
      } catch (e) {
        console.log(e);
      } finally {
        this.isLoading = false;
      }
    },
    async updatePost(e) {
      console.log("đang update post từ body, e:", e);
      console.log("index ", this.active_index);
      console.log("post_ old", this.posts[this.active_index]);
      try {
        this.isLoading = true;
        await postRepository.updatePost(
          this.posts[this.active_index]._id,
          e.textContents,
          e.mediaContents
        );
        console.log("cập nhật thành công");
        this.posts[this.active_index] = await postRepository.getPost(this.posts[this.active_index]._id);
        console.log("post lấy từ server về:", this.posts[this.active_index]);
        // this.posts[this.active_index].title = e.textContents.title;
        // this.posts[this.active_index].feeling = e.textContents.feeling;
        // let newImages = e.textContents.images;
        // e.mediaContents.images.forEach(img => {
        //   newImages.push(URL.createObjectURL(img));
        // });
        // let newVideos = e.textContents.videos;
        // e.mediaContents.videos.forEach(vid => {
        //   newVideos.push(URL.createObjectURL(vid));
        // });
        // let newAudios = e.textContents.audios;
        // e.mediaContents.audios.forEach(aud => {
        //   newAudios.push(aud.src);
        // });
        // this.posts[this.active_index].audios = newAudios;
        // this.posts[this.active_index].videos = newVideos;
        // this.posts[this.active_index].images = newImages;
        // console.log("new_post_updated:",this.posts[this.active_index]);
      } catch (error) {
        //TODO thông báo cập nhập thất bại
        console.log("cập nhập thất bại",error);
      } finally{
      this.isLoading = false;
      }
      // this.isLoading = true;
      // try {
      //   this.posts[i] = await postRepository.getPost(this.posts[i]._id);
      // } catch (error) {
      //   console.log("lỗi update ",error);
      // } finally {
      //   this.isLoading = false;
      // }
    }
  }
};
</script>

<style>
.scroll_body {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 95vh;
}
.scroll_body::-webkit-scrollbar {
  width: 0px;
}
