<template>
  <div class="my-block mr-2">
    <!-- <span>Chứa đựng:</span> -->
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item" v-for="(content,index) in contents" v-bind:key="index">
        {{content.name}}
        <input
          v-model="content.checked"
          type="checkbox"
          class="float-right"
          @change="updateContentFilter"
        />
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contents: [
        {
          name: "Hình ảnh",
          checked: false,
          alias:'images'
        },
        {
          name: "Video",
          checked: false,
          alias:'videos'
        },
        {
          name: "Văn bản",
          checked: false,
          alias:'title'
        },
        {
          name: "Đoạn ghi âm",
          checked: false,
          alias:'audios'
        }
      ]
    };
  },
  methods: {
    updateContentFilter() {
      let contentArr = [];
      for (let i = 0; i < this.contents.length; i++) {
        if (this.contents[i].checked == true) contentArr.push(this.contents[i].alias);
      }
      this.$emit("changed", contentArr);
    }
  }
};
</script>

<style>
.list-group-item {
  border: none;
  background-color: transparent;
}
</style>


