<template>
  <div class="my-block mr-2">
      <!-- <span>Chủ đề</span> -->
      <div v-for="(topic,i) in topics" v-bind:key="i">
            
          <div class="card-topic mt-2 mborder" v-bind:style="'background-image:url('+topic.background+')'">{{topic.name}}
              <input type="checkbox" class="float-right">
          </div>
      </div>
  </div>
</template>

<script>
export default {

    data(){
        return{
            topics:[
                {name:"Gia đình",background:"https://ak9.picdn.net/shutterstock/videos/18935489/thumb/1.jpg",id:"family"},
                {name:"Thể thao",background:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTcUWhW6CxGxwWNKF070brXoPGQxhiIgUhjMQaZUwu0c5WpoB54FQ&s",id:"family"},
                {name:"Du lịch",background:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQXEUT3eAVdjCDWx7rRDGGhkHTK4hF0gCClTzE7KE5D1b6pKyEJ&s",id:"family"},
                {name:"Âm nhạc",background:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT6ySrti6DsbOiwIqGesxeDRPGUGQ5D4aLk7K6gvQMjWMNihThUxQ&s",id:"family"}
            ]
        }
    }

}
</script>

<style>
.card-topic{
    height: 100px;
    color: white;
    /* text-shadow: 2px; */
     -webkit-text-stroke: 0.2px rgba(88, 88, 88, 0.815);
    padding-left: 20px;
    padding-top: 20px;
    padding-right: 20px;
    outline:none !important;
    background-position: center;
}
</style>


