<template>
  <div class="my-block">
      <ul class="list-group-sm no-padding-inline">
          <li class="list-group-item">
              <span>Hiển thị nhật kí bị đóng băng</span>
              <SwitchElement :checked="checked" class="float-right" v-on:updated="$emit('require_password')"></SwitchElement>
              <!-- <AuthenticationPopup></AuthenticationPopup> -->
          </li>
      </ul>
  </div>
</template>

<script>
import SwitchElement from "./../../shared/SwitchElement";
import AuthenticationPopup from "./../../shared/AuthenticationPopup";
export default {
components:{
    SwitchElement,
    AuthenticationPopup
},props:{
    checked:Boolean
}
}
</script>

<style>

</style>


