import {setCookie, deleteAllCookies,getCookie} from "./cookieRepository"
import axios from 'axios';
import 'babel-polyfill'
const ip_api = "http://ip-api.com/json/";
export default {
    async getLocationFromIp(ip){
        try {
            let location = await axios.get(ip_api+"27.79.194.191");
            console.log(location);
            if(location.status=="success")
            return Promise.resolve(location.regionName)
            else return Promise.resolve("Unknow Location");  
        } catch (error) {
            return Promise.resolve("Unknow Location");   
        }
    }
}


