import Repository from "./Repository"
import { setCookie, deleteAllCookies, getCookie } from "./cookieRepository"
import { baseDomain } from "./Repository"
import 'babel-polyfill'
const resource = "/user";
export default {
    login: async function (googleUser) {
        try {
            let result = await Repository.post(`${resource}/login`, googleUser);
            if (result.status == 201) {
                console.log(result.data);
                setCookie("jhash", result.data.token);
                setCookie("_id", result.data._id);
                return Promise.resolve(result.data);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    },
    getProfile: async function (userID) {
        try {
            let result = await Repository.get(`${resource}/profile`, { params: { userID: userID } });
            if (result.status == 200) {
                console.log(result.data);
                let profile = result.data;
                if (!profile.avatar.includes("http"))
                    profile.avatar = `${baseDomain}/img/${profile.avatar}`;
                return Promise.resolve(profile);
            } else return Promise.reject()
        } catch (e) { return Promise.reject() }
    },
    logout() {
        console.log("deleting cookie");
        deleteAllCookies();
        console.log("deleted cookie")

    },
    updateProfile: async function (newProfile, avatar) {
        try {
            let data = new FormData();
            data.append('textContent', JSON.stringify(newProfile));
            if (avatar != null) data.append('avatar', avatar);
            let result = await Repository.put(`${resource}/profile`, data);
            if (result.status == 205) {
                return Promise.resolve();
            } else return Promise.reject(new Error(result.status))
        } catch (e) { return Promise.reject(e) }
    },
    logoutDevice: async function (ip) {
        try {
            if (ip != null) {
                let result = await Repository.delete(`${resource}/devices`, { params: { ip:ip} });
                if (result.status == 205) {
                    return Promise.resolve();
                } else return Promise.reject(new Error(result.status))
            }else return Promise.reject("Unknow device ip")
        } catch (e) { return Promise.reject(e) }
    }
}


