<template>
  <div>
    <div v-if="editing && index>=0" class="edit-post">
      <NewPostEditor :editMode="true" :user="user" :post="post" v-on:edit_canceled="editing=false" v-on:post_updated="update_post"></NewPostEditor>
    </div>
    <div v-if="!editing" class="post mr-2">
      <MoreOptionDialog
      v-if="index>=0"
        class="float-right"
        :post_id="post.id"
        v-on:delete="$emit('delete_action', { id: post.id, index: index })"
        v-on:share="$emit('share_action', { id: post.id, index: index })"
        v-on:freeze="$emit('freeze_action', { id: post.id, index: index })"
        v-on:edit="editing=true"
      ></MoreOptionDialog>
      <div class="show-post">
        <div class="row align-items-start">
          <div class="col-md-auto">
            <img class="post-avatar" :src="user.avatar" />
          </div>
          <div class="col" style="padding-left:0px">
            <div class="row align-items-start">
              <div class="col-md-auto no-padding">
                <span class="post-username">{{ user.name }}</span>
              </div>
              <div class="col" v-show="post.status">
                <span class="input-group mt-1">
                  <icon
                    icon="user-lock"
                    pull="left"
                    size="xs"
                    class="post-icons input-group-prepend"
                  />
                  <span class="mini-text">{{ post.status }}</span>
                </span>
              </div>
            </div>

            <div class="row mt-2">
              <div class="col-md-auto">
                <span class="input-group">
                  <icon
                    icon="clock"
                    pull="left"
                    size="xs"
                    class="post-icons input-group-prepend mt-1"
                  />
                  <p class="mini-text">{{ post.time }}</p>
                </span>
              </div>
              <div class="col align-self-center" v-show="post.location">
                <span class="input-group">
                  <icon
                    icon="map-marked-alt"
                    pull="left"
                    size="xs"
                    class="post-icons input-group-prepend"
                  />
                  <p class="mini-text">{{ post.location }}</p>
                </span>
              </div>
            </div>
          </div>
        </div>
        <hr />
        <div class="row">
          <img class="col-auto" :src="feelingArr[parseInt(post.feeling)]" alt />
          <p v-show="post.title.length" class="col-auto post-content-text mt-3">{{ post.title }}</p>
        </div>
        <div class="grid-container">
          <img
            v-for="(image, ii)  in post.images"
            v-bind:key="'image'+ii"
            :class="'mborder '+((ii==0 || post.images.length <=2)?'single-image':((post.images.length >3)?'grid-image-tripple':'grid-image-double'))"
            :src="image"
            v-show="ii<4"
            v-on:click="onClick(ii)"
          />
          <audio
            style="display:inline-block; outline:none !important; border:none; width:100%"
            v-for="(audio, vi)  in post.audios"
            v-bind:key="'audio'+vi"
            controls
          >
            <source :src="audio" type="audio/ogg" />
            <source src="horse.mp3" type="audio/mpeg" />Your browser does not support the audio element.
          </audio>
          <video
            v-for="(video, vi)  in post.videos"
            v-bind:key="'video'+vi"
            :class="'mborder '+((vi==0 || post.videos.length <=2)?'single-image':((post.videos.length >3)?'grid-image-tripple':'grid-image-double'))"
            controls
          >
            <source :src="video" />
            <source src="movie.ogg" type="video/ogg" />Your browser does not support the video tag.
          </video>
        </div>
        <vue-gallery-slideshow
          :images="post.images"
          :index="image_index"
          @close="image_index = null"
        ></vue-gallery-slideshow>
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import VueGallerySlideshow from "vue-gallery-slideshow";
import GoodActive from "../../shared/assets/good_active.png";
import HateActive from "../../shared/assets/hate_active.png";
import NaturalActive from "../../shared/assets/natural_active.png";
import ExcellentActive from "../../shared/assets/excellent_active.png";
import DisappointedActive from "../../shared/assets/disappointed_active.png";
import NewPostEditor from "../new_post_components/NewPostEditor"

import {
  faClock,
  faMapMarkedAlt,
  faUserLock
} from "@fortawesome/free-solid-svg-icons";

library.add(faClock, faMapMarkedAlt, faUserLock);
import MoreOptionDialog from "./MoreOptionDialog";
export default {
  name:"post",
  created() {
    console.log(this.post.videos);
  },
  data() {
    return {
      editing:false,
      feelingArr: [
        ExcellentActive,
        GoodActive,
        NaturalActive,
        DisappointedActive,
        HateActive
      ],
      image_index: null,
      security: "Bí mật",
      images: this.getImageScr
    };
  },
  props: {
    post: Object,
    index: Number,
    user: Object
  },
  components: {
    MoreOptionDialog,
    VueGallerySlideshow,
    NewPostEditor
  },
  methods: {
    onClick(i) {
      this.image_index = i;
    },
    update_post(e){
      console.log("update content form post " ,e)
      this.$emit("post_updated",e);
      this.editing=false;
    }
  }
};
</script>

<style>
.post {
  border-radius: 32px;
  background-color: white;
  border: solid 1px rgb(177, 177, 177);
  padding: 20px;
}
.post-icons {
  color: black;
  /* width: 40px; */
  /* height: 40px; */
  /* margin: 10px; */
  /* padding: 2px; */
  /* border: solid 0px; */
  /* border-radius: 32px; */
}
.single-image {
  width: 100%;
  padding: 10px;
  height: auto;
}
.grid-image-double {
  width: 50%;
  padding: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.grid-image-tripple {
  width: 30%;
  margin: 10px;
  /* display: grid;
  grid-template-columns: auto auto;
  justify-content: space-evenly;
  grid-gap: 1rem;
  max-width: 100%;
  margin: 1rem auto;
  padding: 0 5rem; */
}
.media-content {
  width: auto;
  border-radius: 32px;
  height: auto;
  object-fit: cover;
  border-radius: 0.75rem;
  display: inline-block;
}
.post-username {
  color: black;
}
.mini-text {
  font-size: 12px;
}
.post-avatar {
  border-radius: 32px;
  width: 40px;
  height: 40px;
}
.post-content-text {
  background-color: rgb(140, 201, 255);
  border-bottom-right-radius: 32px;
  border-bottom-left-radius: 32px;
  border-top-right-radius: 32px;
  border: 1px;
  padding: 10px;
  text-align: center;
  vertical-align: auto;
}
</style>



