<template>
  <div class="input-group input-group-search">
    <input
      class="form-control transparent"
      type="search"
      v-bind:placeholder="placeholder"
      aria-label="Search"
      v-model="searchValue"
      @keyup="enterToSearch"
    />
    <button v-on:click="$emit('search',searchValue)" class="btn input-group-append transparent">
      <icon
            icon="search"
            class="float-right ml-2"
          />
    </button>
  </div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
library.add({faSearch})
export default {
  data() {
    return {
      placeholder:"Tìm kiếm nội dung | chủ đề | cảm xúc .......",
      searchValue:''
    };
  },
  methods:{
    enterToSearch(e){
      console.log(e);
      if(e.code=="Enter") this.$emit("search",this.searchValue)
    }
  }
};
</script>

<style>
.input-group-search{
  border-radius: 32px !important;
  background-color:white;
  padding: 2px;
}
.input-group-search:focus{
  border-radius: 32px !important;
  background-color:white;
  padding: 2px;
}
.transparent{
  border-radius: 32px;
  background-color:transparent;
  border:transparent !important;
  outline:none;
}
.transparentz:focus{
  background-color:transparent;
  border:transparent !important;
  outline:none;
}

</style>



