<template>
    <!-- Button trigger modal -->

    <!-- Modal -->
    <div
      class="modal fade mborder"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="deleteModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="deleteModalTitle">
              {{ delete_modal_title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ delete_modal_body }}
          </div>
          <div class="modal-footer">
            <button v-on:click="$emit('freeze')" type="button" class="btn btn-secondary mborder">
              {{ freeze_btn_str }}
            </button>
            <button v-on:click="$emit('edit')" type="button" class="btn btn-secondary mborder">
              {{ edit_btn_str }}
            </button>
            <button v-on:click="$emit('delete')" type="button" class="btn btn-primary mborder">
              {{ delete_btn_str }}
            </button>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      delete_modal_title: "Xoá kỷ niệm?",
      delete_modal_body:
        "Sau khi xoá kỷ niệm, sẽ không có cách nào để khôi phục, bạn có thể dùng tính năng 'Đóng băng' hoặc 'Chỉnh sửa' như một lựa chọn khác để không bị mất kỷ niệm này!",
        delete_btn_str:"Xoá",
        freeze_btn_str:"Đóng băng",
        edit_btn_str:"Chỉnh sửa"
    };
  }
};
</script>

<style></style>



