<template>
  <div>
    <loading :active.sync="isLoading" :is-full-page="false"></loading>
    <LoginScreen v-if="!isLoged" v-on:logged_in="change_user" @logging="isLoading=true"></LoginScreen>
    <div v-if="isLoged">
      <NavigatorBar
        class="sticky-top"
        v-on:myEvent="change_extension_tab"
        :user="user"
        @search="searchPost"
      ></NavigatorBar>
      <div class="container-fluid">
        <div class="row my-2">
          <div class="col-lg-3 col-md-4 col-sm-none">
            <Calendar @time_filter_changed="apply_time_filter"></Calendar>
          </div>
          <div class="col-lg-6 col-md-4 col-sm-8">
            <Body ref="body" :user="user"></Body>
          </div>
          <div class="col-lg-3 col-md-4 col-sm-6">
            <ExtensionTab
              v-bind:active_tab="active_tab"
              :user="user"
              @logout="logout"
              @apply_filter="refresh_body"
            ></ExtensionTab>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Calendar from "./components/calendar/calendar.vue";
import ExtensionTab from "./components/extensions/ExtensionTab.vue";
import NavigatorBar from "./components/navigationbar/NavigatorBar";
import LoginScreen from "./components/LoginScreen";
import Body from "./components/body/Body.vue";
import { getCookie } from "./repositories/cookieRepository";
import userRepository from "./repositories/userRepository";
import Loading from "vue-loading-overlay";
import { timeout } from "q";

export default {
  name: "app",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      active_tab: 0,
      isLoged: false,
      user: {},
      isLoading: false,
      currentFilter: {}
    };
  },
  async beforeCreate() {
    let uid = getCookie("_id");
    let jhash = getCookie("jhash");
    console.log("id :", uid, " hash :", jhash);
    if (uid == null || jhash == null || uid == undefined || jhash == undefined)
      this.isLoged = false;
    else {
      try {
        let userProfile = await userRepository.getProfile(uid);
        console.log(userProfile);
        this.user = userProfile;
        this.isLoged = true;
      } catch (error) {
        this.isLoged = false;
        console.log(error);
        return;
      }
    }
  },

  components: {
    Calendar,
    ExtensionTab,
    NavigatorBar,
    Body,
    LoginScreen,
    Loading
  },
  methods: {
    apply_time_filter(e){
      this.$refs.body.refreshPosts({time:e});
    },
    change_extension_tab(e) {
      console.log(e);
      if (e.tab_id == "settings") {
        this.active_tab = 2;
      } else if (e.tab_id == "profile") {
        this.active_tab = 1;
      } else this.active_tab = 0; //filter
    },
    change_user(e) {
      console.log("logged success?", e);
      this.isLoged = !this.isLoged;
      this.isLoading=false;
      this.user = e;
      this.$refs.body.refreshPosts();
    },
    async logout() {
      this.isLoading = true;
      await timeout(3000);
      this.isLoged = false;
      this.isLoading = false;
    },
    refresh_body(filter) {
      console.log("refresh ", filter);
      this.currentFilter = filter;
      this.$refs.body.refreshPosts(filter);
    },
    searchPost(e) {
      console.log(e)
      this.currentFilter.title = e;
      this.$refs.body.refreshPosts(this.currentFilter);
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
