<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li
        class="list-group-item"
        v-for="(setting, i) in notificationSettings"
        v-bind:key="i"
      >
        <span>{{ setting.title }}</span>
        <SwitchElement
          class="float-right"
          v-bind:checked="setting.checked"
          v-on:updated="
            setting.checked = !setting.checked;
            log_info();
          "
        ></SwitchElement>
        <ul v-if="setting.checked" class="list-group-sm no-padding-inline">
          <li
            class="list-group-item"
            v-for="(option, i) in setting.options"
            v-bind:key="i"
          >
            <span>{{ option.title }}</span>
            <input
              v-if="option.type == 'frequency'"
              class="input-number"
              type="number"
              v-bind:value="option.values"
            />
            <span>{{ option.unit }}</span>
            <input
              class="float-right"
              type="checkbox"
              v-model="option.checked"
            />
          </li>
          <li class="list-group-item" v-for="(v, i) in setting.via" v-bind:key="i">
            <span>{{ v.method }}</span>
            <input class="float-right" type="checkbox" v-model="v.checked" />
          </li>
        </ul>
      </li>
    </ul>
  </div>
</template>

<script>
import SwitchElement from "../../shared/SwitchElement";
export default {
  data() {
    return {
      remind_write_diary_str: "Nhắc tôi viết nhật ký",
      turn_on_notify_str: "Thông báo các sự kiện",
      notificationSettings: [
        {
          title: "Nhắc tôi viết nhật kí",
          checked: false,
          options: [
            {
              op_id: "remind_when_no_active",
              checked: false,
              title: "Mỗi ngày",
              type: "time",
              values: 1
            },
            {
              op_id: "remind_when_no_active",
              type: "frequency",
              checked: false,
              unit: "ngày",
              title: "Sau khi không có hoạt động trong vòng",
              values: 1
            }
          ],
          via: [
            {
              method: "Gửi thông báo đến Email",
              checked: false
            },
            {
              method: "Thông báo qua ứng dụng",
              checked: false
            }
          ]
        },
        {
          title: "Khi đăng nhập trên thiết bị mới",
          checked: false,
          options: [],
          via: [
             {
              method: "Gửi thông báo đến Email",
              checked: false
            },
            {
              method: "Thông báo qua ứng dụng",
              checked: false
            }
          ]
        },
        {
          title: "Xem lại các kỷ niệm theo chủ đề",
          checked: false,
          options: [
            {
              title: "Gia đình",
              status: "checked"
            },
            {
              title: "Thể thao",
              status: "checked"
            },
            {
              title: "Du lịch",
              status: "checked"
            },
            {
              title: "Âm nhạc",
              status: "checked"
            }
          ],
          via: [
            {
              method: "Gửi thông báo đến Email",
              checked: false
            },
            {
              method: "Thông báo qua ứng dụng",
              checked: false
            }
          ]
        },
        {
          title: "Gửi cho tôi các mẹo sử dụng ứng dụng",
          checked: false,
          options: [],
          via: [
            {
              method: "Gửi thông báo đến Email",
              checked: false
            },
            {
              method: "Thông báo qua ứng dụng",
              checked: false
            }
          ]
        }
      ]
    };
  },
  components: {
    SwitchElement
  },
  methods: {
    log_info() {
      console.log("adfasdfads", this.notificationSettings);
    }
  }
};
</script>

<style>

.input-number {
  outline: none;
  width: 60px;
  border-radius: 32px;
  text-align: center;
}
</style>



