<template>
  <div>
    <CollapseButton icon="quote-left" name="Nội dung"></CollapseButton>
    <ContentFilter
      class="collapse show"
      id="id_quote-left_element"
      @changed="update_content_filter"
    ></ContentFilter>
    <CollapseButton icon="grin-beam" name="Cảm xúc"></CollapseButton>
    <FeelingFilter
      class="collapse show"
      id="id_grin-beam_element"
      @changed="update_feeling_filter"
    ></FeelingFilter>
    <CollapseButton icon="air-freshener" name="Chủ đề"></CollapseButton>
    <TopicFilter class="collapse show" id="id_air-freshener_element"></TopicFilter>
  </div>
</template>

<script>
import CollapseButton from "./../../shared/CollapseButton";
import ContentFilter from "./ContentFilter";
import FeelingFilter from "./FeelingFilter";
import TopicFilter from "./TopicFilter";
export default {
  data() {
    return {
      haveContentArr: [],
      haveFeelingArr: []
    };
  },
  components: {
    CollapseButton,
    TopicFilter,
    ContentFilter,
    FeelingFilter
  },
  methods: {
    update_content_filter(contents) {
      console.log("content filter receive: ", contents)
      this.haveContentArr = contents;
      this.$emit("filter_changed", {
        contents: this.haveContentArr,
        feelings: this.haveFeelingArr
      });
    },
    update_feeling_filter(feelings) {
      console.log("here")
      console.log("feeling emit from FeelingFilter.vue",feelings)
      this.haveFeelingArr = feelings;
      console.log("feeling",this.haveFeelingArr)
      this.$emit("filter_changed", {
        contents: this.haveContentArr,
        feelings: this.haveFeelingArr
      });
    }
  }
};
</script>

<style>
</style>



