<template>
  <div class="extensiontab">
    <loading :active.sync="isLoading" :is-full-page="false"></loading>
    <div class="row">
      <div class="col-6">
        <h3>{{ tabs[active_tab].tab_name }}</h3>
      </div>
      <div class="col-6 mt-1">
        <button
          v-on:click="updateExtension"
          v-if="tabs[active_tab].btn_fn_name != ''"
          class="btn btn-function float-right"
        >{{ tabs[active_tab].btn_fn_name }}</button>
      </div>
    </div>
    <hr class="mr-3" />
    <Filters
      class="scroll_extensions"
      v-if="active_tab == 0"
      :user="user"
      @filter_changed="change_filter"
    ></Filters>
    <Profile
      class="scroll_extensions"
      v-if="active_tab == 1"
      :user="user"
      @logout="$emit('logout')"
      @avatar_changed="change_avatar"
      @profile_changed="change_profile"
    ></Profile>
    <Setting class="scroll_extensions" v-if="active_tab == 2" :user="user" @loading="isLoading=!isLoading" @logout="$emit('logout')"></Setting>
  </div>
</template>

<script>
import Profile from "./Profile";
import Filters from "./filters/Filters";
import Setting from "./settings/Setting";
import userRepository from "../../repositories/userRepository";
import Loading from "vue-loading-overlay";
import postRepository from "../../repositories/postRepository";
export default {
  components: {
    Profile,
    Setting,
    Filters,
    Loading
  },
  data() {
    return {
      tabs: [
        {
          tab_name: "Bộ lọc",
          btn_fn_name: "Áp dụng"
        },
        {
          tab_name: "Hồ sơ cá nhân",
          btn_fn_name: "Cập nhập"
        },
        {
          tab_name: "Cài đặt",
          btn_fn_name: ""
        }
      ],
      isLoading: false,
      profile_temp: {},
      avatar_file: "",
      filter: {}
    };
  },
  props: {
    active_tab: 0,
    user: Object
  },
  methods: {
    async updateExtension() {
      console.log("cập nhập ở ", this.active_tab);
      if (this.active_tab === 1) {
        //cập nhập profile
        this.isLoading = true;
        try {
          await userRepository.updateProfile(
            this.profile_temp,
            this.avatar_file
          );
        } catch (e) {
          console.log(e);
        } finally {
          this.isLoading = false;
        }
      } else if (this.active_tab === 0) {
        try {
          console.log(this.filter);
          this.$emit("apply_filter", this.filter);
        } catch (e) {
          console.log(e);
        } finally {
          this.isLoading = false;
        }
      }
    },
    change_avatar(file) {
      this.avatar_file = file;
    },
    change_profile(profile) {
      this.profile_temp = profile;
    },
    change_filter(filter) {
      this.filter = filter;
    }
  }
};
</script>

<style>
.extensiontab {
  border-radius: 32px;
  background-color: whitesmoke;
  padding-top: 20px;
  padding-left: 20px;
  padding-bottom: 20px;
  height: 98vh;
  /* margin-right: 200px; */
  /* padding-right: 50px; */
}
.scroll_extensions {
  overflow-y: scroll;
  overflow-x: hidden;
  height: 86vh;
}
.scroll_extensions::-webkit-scrollbar {
  width: 3px;
}

.scroll_extensions::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 32px;
}

.scroll_extensions::-webkit-scrollbar-thumb:hover {
  background: red;
}
.btn-function {
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:hover {
  background-color: turquoise;
  border-radius: 32px;
  margin-right: 10px;
}
.btn-function:active {
  background-color: turquoise !important;
  border-radius: 32px;
  margin-right: 10px;
}
</style>



