<template>
  <div class="my-block">
    <ul class="list-group-sm no-padding-inline">
      <li class="list-group-item">
        <span>Mật khẩu ứng dụng</span>
        <SwitchElement id="app_password" :checked="checked" class="float-right" v-on:updated="protect"></SwitchElement>
      </li>
      <li class="list-group-item">
        <span>Mật khẩu cài đặt</span>
        <SwitchElement class="float-right" v-on:updated="$emit('require_password')"></SwitchElement>
      </li>
      <li class="list-group-item">
        <a href="">Đổi mật khẩu</a>
      </li>
    </ul>
  </div>
</template>

<script>
import SwitchElement from "../../shared/SwitchElement";
export default {
  components: { SwitchElement },
  methods:{
    protect(e){
      this.$emit("require_password")
      console.log(e);
    }
  },
  props:{
    checked:Boolean
  }
};
</script>

<style></style>



