<template>
  <div class="dropdown mborder">
    <a
    href="#"
      class="dropdown-toggle"
      id="dropdownMenuButton"
      data-toggle="dropdown"
      aria-haspopup="true"
      aria-expanded="false"
    ></a>
    <div
      class="dropdown-menu dropdown-menu-right"
      aria-labelledby="dropdownMenuButton"
    >
      <a class="dropdown-item" v-on:click="$emit('share')">{{ share_post }}</a>
      <a class="dropdown-item" v-on:click="$emit('edit')">{{ edit_post }}</a>
      <a class="dropdown-item" v-on:click="$emit('delete')">{{ delete_post }}</a>
      <a class="dropdown-item" v-on:click="$emit('freeze')">{{ frezee_post }}</a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      share_post: "Chia sẻ",
      edit_post: "Chỉnh sửa",
      delete_post: "Xoá",
      frezee_post: "Đóng băng"
    };
  },
  props:{
    post_id:String
  },
  methods:{
    share_this_post:function(){
      console.log(this.post_id);
    }
  }
};
</script>

<style>
.more-btn {
  font-size: 20px;
  color: black;
  background-color: transparent;
  outline: none;
  border: none;
}
.more-btn:hover {
  font-size: 20px;
  color: red;
  background-color: transparent;
  outline: none;
  border: none;
}
.more-btn:visited {
  font-size: 20px;
  color: red;
  background-color: transparent;
  outline: none;
  border: none;
}
.dropdown-menu{
  border-radius:32px;
}
.dropdown-item:hover{
  background-color: transparent;
  color: rgb(4, 0, 219) !important;
  cursor: pointer;
}
.modal-content{
  border-radius: 32px;
}
</style>



