<template>
  <div class="login-div my-container" v-bind:style="'background-image:url('+background_image+')'">
    <div class="input-group login-center mborder">
      <img class="big-logo mb-2" src="./navigationbar/logo.png" />
      <br />
      <h1>QDiary</h1>
      <h5 class="slogan">Lưu trữ mọi kỷ niệm</h5>
      <!-- <button class="btn mborder" v-on:click="login">
        <img
          width="20px"
          alt="Google Logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
        />
        Đăng nhập bằng tài khoản Google
      </button>-->
      <GoogleLogin :params="params" :onSuccess="onSuccess" :onFailure="onFailure" class="btn mborder">
        <img
          width="20px"
          alt="Google Logo"
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
        />
        Đăng nhập bằng tài khoản Google
      </GoogleLogin>
    </div>
  </div>
</template>

<script>
import GoogleLogin from "vue-google-login";
import userRepository from "../repositories/userRepository"
export default {
  data() {
    return {
      params: {
        client_id: "571858267778-ogimpuu5ekdli8o3u4o4qbe32o90dtpj.apps.googleusercontent.com",
        client_secret:"4EPf0Y1jINyaQ2j-N-PNuLR5"
      },
      background_image:
        "https://images.unsplash.com/photo-1471107340929-a87cd0f5b5f3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=966&q=80"
    };
  },
  methods: {
    login() {},
    async onSuccess(googleUser) {
    //   console.log(googleUser);
      // This only gets the user information: id, name, imageUrl and email
      this.$emit("logging")
      let result = await userRepository.login(googleUser.getBasicProfile());
      this.$emit("logged_in",result)
    },
    onFailure(fail){
        console.log(fail)
    }
  },
  components: {
    GoogleLogin
  }
};
</script>

<style>
.big-logo {
  width: 100px;
}
.my-container {
  height: 100vh;
  background-position: center; /* Center the image */
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}
.slogan {
  font-style: italic;
}
.login-center {
  background-color: rgba(255, 255, 255, 0.85);
  display: inline-block;
  position: relative;
  top: 30%;
  width: 400px;
  margin: 0 auto;
  padding: 50px;
  z-index: 9999990;
}
</style>


