<template>
    <div
      class="modal fade mborder"
      id="freezeModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="freezeModalTitle"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5
              class="modal-title"
              id="freezeModalTitle"
            >
              {{ freeze_modal_title }}
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            {{ freeze_modal_body }}
          </div>
          <div class="modal-footer">
            <button
              v-on:click="$emit('freeze')"
              type="button"
              class="btn btn-primary mborder"
            >
              {{ freeze_btn_str }}
            </button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      freeze_modal_title: "Đóng băng kỷ niệm?",

      freeze_modal_body:
        "Sau khi đóng băng kỷ niệm, kỷ niệm sẽ không hiển thị trên nhật ký nữa, để có thể hiển thị lại kỷ niệm, hãy vào mục 'Cài đặt'-> 'Nhật kí đóng băng'",

      freeze_btn_str: "Đóng băng",
    };
  }
};
</script>

<style></style>



